<template>
	<div class="new-address-container">
		<topNav class="topNav" :title="title" :isleftarrow="isleftarrow"></topNav>
		<van-address-edit :address-info="addressInfo"
		  :area-list="areaList"
		  show-postal
		  :show-delete="status==1"
		  show-set-default
		  @save="onSave"
		  @delete="onDelete" />
	</div>
</template>

<script>
	import util from '@/common/util/util'
	import areaList from '../../common/area/area.js'
	import topNav from '../../components/topNav.vue'
	export default {
		data() {
			return {
				status: 0, // 0添加地址,1修改地址
				title: '',
				isleftarrow: '',
				areaList,
				addressInfo: {},
				searchResult: []
			}
		},
		components: {
			topNav
		},
		created() {
			if (this.$route.query.id) {
				this.status = 1;
				this.$request.get('/user/shipping-address/detail/v2', {
					id: this.$route.query.id,
					token: util.storage.get('token')
				}).then(res => {
					console.log(res)
					this.addressInfo = {
						id: res.data.info.id,
						name: res.data.info.linkMan,
						tel: res.data.info.mobile,
						province: res.data.info.provinceStr,
						city: res.data.info.cityStr,
						county: res.data.info.areaStr,
						addressDetail: res.data.info.address,
						postalCode: res.data.info.code,
						areaCode: res.data.info.districtId,
						isDefault: res.data.info.isDefault
					}
				});
			}
		},
		mounted() {
			this.title = this.$route.meta.title;
			this.isleftarrow = this.$route.meta.isleftarrow;
		},
		methods: {
			onSave(list) {
				this.$toast('save');
				console.log(list);
				this.updateAddress(list)
			},
			updateAddress(form) {
				let addressAction = 'update';
				let params = {}
				this.$route.query.id ? addressAction = 'update' : addressAction = 'add'
				params = {
					id: this.$route.query.id,
					token: util.storage.get('token'),
					linkMan: form.name,
					mobile: form.tel,
					isDefault: form.isDefault,
					address:form.addressDetail,
					provinceId: form.areaCode.slice(0, 2) + '0000',
					cityId: form.areaCode.slice(0, 4) + '00',
					districtId: form.areaCode,
					code: form.postalCode
				}
				this.$toast.loading({
					mask: true,
					message: '地址数据提交中...',
					duration: 0
				})
				this.$request.post(`/user/shipping-address/${addressAction}`, params).then(res => {
					// 已参团不做处理，提交订单时后端会提示
					if (res.code !== 0) {
						this.$toast(res.msg)
						return
					}
					this.$toast.clear()
					this.$router.go(-1)
				})
			},
			deleteAddress(params) {
			  this.$toast.loading({
			    mask: true,
			    message: '地址数据提交中...',
			    duration: 0
			  })
			  this.$request.post('/user/shipping-address/delete', params).then(res => {
			    if (res.code !== 0) {
			      this.$toast(res.msg)
			    }
			    this.$toast.clear()
			    this.$router.go(-1)
			  })
			},
			onDelete() {
			  this.deleteAddress({
			    token: util.storage.get('token'),
			    id: this.addressInfo.id
			  })
			},
			onChangeDetail(val) {
				// if (val) {
				// 	this.searchResult = [{
				// 		name: '黄龙万科中心',
				// 		address: '杭州市西湖区',
				// 	}, ];
				// } else {
				// 	this.searchResult = [];
				// }
			},
		}
	}
</script>

